.header {
  @include bg-image-settings(center, cover);
  background-image: url(../images/header.jpg);

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0 80px;
  }

  &__call {
    text-decoration: none;

    @include onDesktop {
      &::before {
        content: "+1 234 555-55-55";
        position: relative;
        top: 50%;
        transform: translate(-100%, 50%);

        display: block;
        width: max-content;
        padding-right: 20px;
        @include font-settings(bold, 12px, 16px);
        color: $colorWhite;
        letter-spacing: 2px;
        text-transform: uppercase;
        opacity: 0;
        pointer-events: none;
      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &__title {
    @include font-settings(bold, 46px, 40px);
    color: $colorWhite;
    letter-spacing: -1px;

    @include onDesktop {
      @include font-settings(bold, 80px, 80px);
      letter-spacing: -1.5px;
    }

    &::before {
      content: "";
      display: block;
      width: 40px;
      height: 2px;
      background-color: $colorWhite;
      margin-bottom: 24px;
    }
  }
}
